import axios from "../../config/axiosConfig";
import { API } from "../../Backend";

import decryptResponse from "../../Shared/decryptResponse";
import encryptResponse from "../../Shared/encryptResponse";

export const createNotification = async (data, businessId) => {
  try {
    const res = await fetch(`${API}/add-notification`, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        "X-BusinessId": businessId || localStorage.getItem("businessId"),
        "X-Auth-Token": localStorage.getItem("token"),
      },
      body: JSON.stringify(encryptResponse(data)),
    });
    return await res.json();
  } catch (err) {
    return { error: err.message };
  }

  /*try {
    const res = await axios({
      baseURL: "/add-notification",
      data: encryptResponse(data),
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
        "X-BusinessId": businessId || localStorage.getItem("businessId"),
      },
    });

    return res.data;
  } catch (err) {
    return { error: err.message };
  } */
};

export const getNotifications = async (businessId) => {
  try {
    const res = await axios({
      url: `/get-notifications`,
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        "X-BusinessId": businessId || localStorage.getItem("businessId"),
        "X-Auth-Token": localStorage.getItem("token"),
      },
    });

    return decryptResponse({ data: res.data });
    // return res.data;
  } catch (err) {
    return { error: err.message };
  }
};

export const getNotificationById = async (id, businessId) => {
  try {
    const res = await axios({
      url: `/get-notification/${id}`,
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
        "X-BusinessId": businessId || localStorage.getItem("businessId"),
      },
    });

    return decryptResponse({ data: res.data });
    // return res.data;
  } catch (err) {
    return { error: err.message };
  }
};
export const updateNotificationById = async (id, data, businessId) => {
  try {
    const res = await axios({
      url: `/update-notification/${id}`,
      method: "PUT",
      data: encryptResponse(data),
      headers: {
        "Content-Type": "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
        "X-BusinessId": businessId || localStorage.getItem("businessId"),
      },
    });

    return res.data;
  } catch (err) {
    return { error: err.message };
  }
};

