export const updateNotificationStatus = async (id, status, businessId) => {
  try {
    const res = await axios({
      url: `/update-notification-status/${id}?status=${status}`,
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
        "X-Auth-Token": localStorage.getItem("token"),
        "X-BusinessId": businessId || localStorage.getItem("businessId"),
      },
    });

    return res.data;
  } catch (err) {
    return { error: err.message };
  }
};
