import React, { useContext, useState } from "react";

export const SystemSchedule = React.createContext();

export const useSystemSchedule = () => {
  return useContext(SystemSchedule);
};

const SystemScheduleContext = ({ children }) => {
  const [formData, setFormData] = useState({
    title: {
      type: "text",
      value: "",
      placeholder: "Notification Title",
      required: true,
      id: 1,
    },
    time: {
      type: "date",
      formate: "hours",
      value: new Date(),
      required: true,
      id: 2,
    },
    days: {
      type: "selection",
      id: 3,
    },
    whom: {
      type: "dropdown",
      value: "",
      options: [
        { name: "For LMS Dealers", value: "lms", id: 1 },
        { name: "For Team Heads", value: "team-head", id: 2 },
        { name: "For Department Heads", value: "department-head", id: 3 },
        { name: "For Admin", value: "business-admin", id: 4 },
      ],
      placeholder: "To Whom?",
      id: 4,
    },
    range: {
      type: "dropdown",
      value: "",
      options: [
        { name: "1 Hour", value: 1, id: 1 },
        { name: "2 Hours", value: 2, id: 2 },
        { name: "3 Hours", value: 3, id: 3 },
        { name: "4 Hours", value: 4, id: 4 },
        { name: "5 Hours", value: 5, id: 5 },
        { name: "6 Hours", value: 6, id: 6 },
        { name: "7 Hours", value: 7, id: 7 },
        { name: "8 Hours", value: 8, id: 8 },
        { name: "9 Hours", value: 9, id: 9 },
        { name: "10 Hours", value: 10, id: 10 },
        { name: "11 Hours", value: 11, id: 11 },
        { name: "13 Hours", value: 13, id: 13 },
        { name: "14 Hours", value: 14, id: 14 },
        { name: "15 Hours", value: 15, id: 15 },
        { name: "16 Hours", value: 16, id: 16 },
        { name: "17 Hours", value: 17, id: 17 },
        { name: "18 Hours", value: 18, id: 18 },
        { name: "19 Hours", value: 19, id: 19 },
        { name: "20 Hours", value: 20, id: 20 },
        { name: "21 Hours", value: 20, id: 21 },
        { name: "22 Hours", value: 20, id: 22 },
        { name: "23 Hours", value: 20, id: 23 },
        { name: "24 Hours", value: 20, id: 24 },
      ],
      placeholder: "Select Range",
      id: 5,
    },
    sms: {
      type: "switch",
      message: `Dear *name*, You have *quantity* new enquiries on https://easysocial.io. For HELP msg on www.e-so.in/helpdesk`,
      status: true,
      id: 6,
      disabled: true,
    },
    email: {
      type: "switch",
      status: false,
      subject: "",
      message: "",
      id: 7,
    },
  });

  const [date, setDate] = useState({ to: new Date(), from: new Date() });

  const [days, setDays] = useState({
    options: [
      { name: "Sun", id: "Sunday", active: false },
      { name: "Mon", id: "Monday", active: true },
      { name: "Tue", id: "Tuesday", active: true },
      { name: "Wed", id: "Wednesday", active: true },
      { name: "Thu", id: "Thursday", active: true },
      { name: "Fri", id: "Friday", active: true },
      { name: "Sat", id: "Saturday", active: false },
    ],
  });

  const [isOpenAddNotification, setOpenAddNotification] = useState(false);

  const [isOpenDeleteDialog, setOpenDeleteDialog] = useState(false);

  const [isOpenEditDialog, setOpenEditDialog] = useState(false);

  const [notificationsList, addNotifications] = useState([]);

  const [selectedNotificationId, setNotificationId] = useState(null);

  const value = {
    date,
    setDate,
    formData,
    setFormData,
    days,
    setDays,
    isOpenAddNotification,
    setOpenAddNotification,
    isOpenDeleteDialog,
    setOpenDeleteDialog,
    notificationsList,
    addNotifications,
    isOpenEditDialog,
    setOpenEditDialog,
    selectedNotificationId,
    setNotificationId,
  };

  return (
    <SystemSchedule.Provider value={value}>{children}</SystemSchedule.Provider>
  );
};

export default SystemScheduleContext;
