import { IconButton, List, Menu, MenuItem } from "@material-ui/core";
import { ListItem } from "@material-ui/core";
import { Button } from "@material-ui/core";
import React from "react";
import classes from "./NotificationList.module.css";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import { useSystemSchedule } from "../../../contexts/SystemScheduleContext";
import { tConvert } from "../../../Shared/Utility";
import {
  getNotifications,
  updateNotificationStatus,
} from "../../../api/systemSchedule";
import { useToastMessage } from "../../../contexts/ToastMessageContext";
import { useLoader } from "../../../contexts/LoaderContext";

const NotificationListItem = ({ text, title, id, status, businessId }) => {
  const {
    setNotificationId,
    setOpenEditDialog,
    selectedNotificationId,
    setOpenDeleteDialog,
    addNotifications,
  } = useSystemSchedule();

  const { setErrorMessage, setSuccessMessage } = useToastMessage();

  const { startLoader, cancelLoader } = useLoader();

  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <ListItem className={classes.ListItem}>
      <div className={classes.ListItem_Detail}>
        <h6>{title}</h6>
        <p>{text}</p>
      </div>
      <div style={{ position: "relative" }}>
        <IconButton style={{ boxShadow: "unset" }} onClick={handleClick}>
          <MoreVertIcon />
        </IconButton>
        <Menu
          getContentAnchorEl={null}
          id="simple-menu"
          anchorEl={anchorEl}
          keepMounted
          open={Boolean(anchorEl)}
          onClose={handleClose}
          anchorOrigin={{
            vertical: "bottom",
            horizontal: "center",
          }}
          transformOrigin={{
            vertical: "top",
            horizontal: "right",
          }}
        >
          <MenuItem
            onClick={() => {
              handleClose();
              id !== selectedNotificationId && setNotificationId(id);
              setOpenEditDialog(true);
            }}
          >
            Edit
          </MenuItem>
          <MenuItem
            onClick={() => {
              handleClose();
              id !== selectedNotificationId && setNotificationId(id);
              setOpenDeleteDialog(true);
            }}
          >
            Delete
          </MenuItem>
          <MenuItem
            onClick={async () => {
              handleClose();
              startLoader();
              id !== selectedNotificationId && setNotificationId(id);

              const res = updateNotificationStatus(id, !status, businessId);

              if (res.error) {
                cancelLoader();
                return setErrorMessage("Something went wron!");
              }

              const resGet = await getNotifications(businessId);

              if (resGet.error) {
                return setErrorMessage(resGet.error);
              }

              addNotifications(resGet.data);

              cancelLoader();

              setSuccessMessage("Cron status successfully changed!");
            }}
          >
            {status ? "Turn off" : "Turn On"}
          </MenuItem>
        </Menu>
      </div>
    </ListItem>
  );
};

const NotificationLists = ({ businessId }) => {
  const { setOpenAddNotification, notificationsList } = useSystemSchedule();

  return (
    <div className={classes.NotificationList}>
      <h1 className={classes.List_Title}>LMS Notifications</h1>

      <Button
        className={classes.CreateNotificationButton}
        onClick={() => setOpenAddNotification(true)}
      >
        Create New
      </Button>

      <List className={classes.List}>
        {notificationsList.length > 0 &&
          notificationsList.map((notification) => {
            const time = tConvert(
              `${new Date(notification.time).getHours()}:${new Date(
                notification.time
              ).getMinutes()}`
            );

            const days = notification.days.map((day) =>
              day.replace("day", "").toLowerCase()
            );

            const team_members = notification.scheduled_for;

            const duration_to = `${notification.reminder_duration} Hours`;

            const smsStatus = notification.sms_message.active;
            const emailStatus = notification.email_message.active;

            return (
              <NotificationListItem
                businessId={businessId}
                key={notification._id}
                id={notification._id}
                title={notification.name || "No Title"}
                text={`${time} - ${days.join(
                  ", "
                )}, Team Members - ${team_members}, ${duration_to}${
                  smsStatus ? " - SMS" : " "
                } - ${emailStatus ? ", Email" : " "}`}
                status={notification.status}
              />
            );
          })}
      </List>
    </div>
  );
};

export default NotificationLists;
