import {
  Input,
  List,
  ListItemText,
  MenuItem,
  Select,
  TextField,
  Switch,
  withStyles,
  FormControlLabel,
  Button,
} from "@material-ui/core";
import { MuiPickersUtilsProvider, TimePicker } from "@material-ui/pickers";
import React, { useEffect, useRef, useState } from "react";
import classes from "./EditNotificationPopup.module.css";
import DateFnsUtils from "@date-io/date-fns";
import "./global.css";
import { useSystemSchedule } from "../../../contexts/SystemScheduleContext";
import { ListItem } from "@material-ui/core";
import clsx from "clsx";
import { CustomButton } from "../../exports";
import { updateObject } from "../../../Shared/Utility";
import { Backdrop } from "../../exports";
import { motion } from "framer-motion";
import {
  getNotificationById,
  getNotifications,
  updateNotificationById,
} from "../../../api/systemSchedule";
import { useToastMessage } from "../../../contexts/ToastMessageContext";

const IOSSwitch = withStyles((theme) => ({
  root: {
    width: 42,
    height: 26,
    padding: 0,
    margin: theme.spacing(1),
    marginRight: 0,
  },
  switchBase: {
    padding: 1,
    color: theme.palette.common.white,
    "&$checked": {
      transform: "translateX(16px)",
      color: theme.palette.common.white,
      "& + $track": {
        backgroundColor: "#12B0EE",
        opacity: 1,
        border: "none",
      },
    },
    "&$focusVisible $thumb": {
      color: "#12B0EE",
      border: "6px solid #fff",
    },
  },
  thumb: {
    width: 24,
    height: 24,
  },
  track: {
    borderRadius: 26 / 2,
    border: `1px solid ${theme.palette.grey[400]}`,
    backgroundColor: theme.palette.grey[50],
    opacity: 1,
    transition: theme.transitions.create(["background-color", "border"]),
  },
  checked: {},
  focusVisible: {},
}))(({ classes, ...props }) => {
  return (
    <Switch
      focusVisibleClassName={classes.focusVisible}
      disableRipple
      classes={{
        root: classes.root,
        switchBase: classes.switchBase,
        thumb: classes.thumb,
        track: classes.track,
        checked: classes.checked,
      }}
      {...props}
    />
  );
});

const SelectDaysComponent = ({ days, setDays }) => {
  const handleSelectActiveDays = (index) => {
    const options = days.options;

    options[index] = {
      ...options[index],
      active: !options[index].active,
    };

    return setDays({ ...days, options: options });
  };

  return (
    <div className={classes.SelectDays_Container}>
      <h1>Select Days</h1>
      <List className={classes.List}>
        {days.options.map((option, i) => {
          const active = option.active;
          return (
            <ListItem
              onClick={() => handleSelectActiveDays(i)}
              button
              key={option.id}
              className={classes.ListItem}
            >
              <ListItemText
                className={clsx(classes.ListItemText, active && classes.Active)}
              >
                {option.name}
              </ListItemText>
            </ListItem>
          );
        })}
      </List>
    </div>
  );
};

const DropdownForToWhom = ({ placeholder, formData, setFormData, id }) => {
  const handleChange = (e) => {
    return setFormData({
      ...formData,
      [id]: { ...formData[id], value: e.target.value },
    });
  };

  return (
    <div className={classes.DropdownForToWhom}>
      <Select
        required={true}
        value={formData[id].value}
        onChange={handleChange}
        className={classes.Select}
        displayEmpty
        inputProps={{ "aria-label": "Without label" }}
      >
        <MenuItem value="" className={classes.Option_TOWhom} disabled>
          {placeholder}
        </MenuItem>
        {formData[id].options.map((option) => {
          return (
            <MenuItem key={option.id} value={option.value}>
              {option.name}
            </MenuItem>
          );
        })}
      </Select>
    </div>
  );
};

const SwitchContainerForSMS = ({ formData, setFormData }) => {
  const onChange = (key, id, e) => {
    return setFormData({
      ...formData,
      [key]: { ...formData[key], [id]: e.target.value },
    });
  };

  const handleSwitch = (key, e) => {
    return setFormData({
      ...formData,
      [key]: { ...formData[key], status: e.target.checked },
    });
  };

  const status = formData.sms.status;

  return (
    <div className={classes.SwitchContainerForSMS}>
      <div className={classes.Switch_Box}>
        <h1>SMS</h1>
        <FormControlLabel
          control={
            <IOSSwitch
              onChange={(e) => handleSwitch("sms", e)}
              checked={formData.sms.status}
              name="checkedB"
            />
          }
        />
      </div>

      {status && (
        <>
          <TextField
            required={true}
            disabled={true}
            className={classes.Input}
            placeholder="Message"
            value={formData.sms.message}
            onChange={(e) => onChange("sms", "message", e)}
            multiline
            rows="3"
          />
        </>
      )}
    </div>
  );
};

const SwitchContainerForEmail = ({ formData, setFormData }) => {
  const onChange = (key, id, e) => {
    return setFormData({
      ...formData,
      [key]: { ...formData[key], [id]: e.target.value },
    });
  };

  const handleSwitch = (key, e) => {
    return setFormData({
      ...formData,
      [key]: { ...formData[key], status: e.target.checked },
    });
  };

  const status = formData.email.status;

  return (
    <div className={classes.SwitchContainerForEmail}>
      <div className={classes.Switch_Box}>
        <h1>Email</h1>
        <FormControlLabel
          control={
            <IOSSwitch
              onChange={(e) => handleSwitch("email", e)}
              checked={formData.email.status}
              name="checkedB"
            />
          }
        />
      </div>
      {status && (
        <>
          <TextField
            required={true}
            className={classes.Input}
            placeholder="Subject"
            value={formData.email.subject}
            onChange={(e) => onChange("email", "subject", e)}
            multiline
            rows="1"
          />
          <TextField
            required={true}
            className={classes.Input}
            placeholder="Message"
            value={formData.email.message}
            onChange={(e) => onChange("email", "message", e)}
            multiline
            rows="3"
          />
        </>
      )}
    </div>
  );
};

const FormElements = ({ days, setDays, formData, setFormData }) => {
  const { setErrorMessage, setSuccessMessage, isDisabled, setDisabled } =
    useToastMessage();

  const {
    setOpenEditDialog,
    selectedNotificationId,
    setNotificationId,
    addNotifications,
  } = useSystemSchedule();

  const inputChangedHandler = (event, inputIdentifier) => {
    let updatedFormElement;

    updatedFormElement = updateObject(formData[inputIdentifier], {
      value: event.target.value,
    });

    const updatedOrderForm = updateObject(formData, {
      [inputIdentifier]: updatedFormElement,
    });

    setFormData(updatedOrderForm);
  };

  const onSubmit = async (event) => {
    event.preventDefault();

    setDisabled();

    const activeDays = [];

    days.options.filter((option) => {
      if (option.active) {
        activeDays.push(option.id);
      }

      return option;
    });

    const data = {
      name: formData.title.value,
      days: activeDays,
      reminder_duration: formData.range.value,
      status: true,
      sms_message: {
        active: formData.sms.status,
        message: formData.sms.message,
      },
      email_message: {
        active: formData.email.status,
        message: formData.email.message,
        subject: formData.email.subject,
      },
      time: formData.time.value,
      scheduled_for: "all",
      type: formData.whom.value,
    };

    const res = await updateNotificationById(selectedNotificationId, data);

    if (res.error) {
      return setErrorMessage(res.error);
    }

    const resGet = await getNotifications();

    if (resGet.error) {
      return setErrorMessage("You may not have created notifications!");
    }

    addNotifications(resGet.data);

    setOpenEditDialog(false);

    return setSuccessMessage(res.message);
  };

  const formElementsArray = [];

  for (let key in formData) {
    formElementsArray.push({
      id: key,
      config: formData[key],
    });
  }

  return (
    <form className={classes.Form_Container} onSubmit={onSubmit}>
      {formElementsArray.map((formElement) => {
        const type = formElement.config.type;
        const id = formElement.id;

        if (type === "text" || type === "number" || type === "email") {
          const value = formElement.config.value;
          const placeholder = formElement.config.placeholder;
          return (
            <div key={id} className={classes.InputContainer}>
              <Input
                onChange={(e) => {
                  inputChangedHandler(e, id);
                }}
                required={formElement.config.required}
                className={classes.Input}
                value={value}
                placeholder={placeholder}
              />
            </div>
          );
        }

        if (type === "date") {
          const value = formElement.config.value;

          return (
            <div key={id} className={classes.TimePicker_Container}>
              <h1>What time of the Day?</h1>

              <MuiPickersUtilsProvider
                utils={DateFnsUtils}
                className={classes.TimePicker}
              >
                <TimePicker
                  className={classes.Picker}
                  margin="normal"
                  id="time-picker"
                  onChange={(date) =>
                    inputChangedHandler({ target: { value: date } }, id)
                  }
                  value={value || new Date()}
                  KeyboardButtonProps={{
                    "aria-label": "change time",
                  }}
                  placeholder="What time of the day?"
                />
              </MuiPickersUtilsProvider>
            </div>
          );
        }

        if (type === "selection") {
          return <SelectDaysComponent days={days} setDays={setDays} key={id} />;
        }

        if (type === "dropdown") {
          return (
            <DropdownForToWhom
              setFormData={setFormData}
              formData={formData}
              key={id}
              id={id}
              placeholder={formElement.config.placeholder}
            />
          );
        }

        if (type === "switch") {
          if (id === "sms") {
            return (
              <>
                <SwitchContainerForSMS
                  setFormData={setFormData}
                  formData={formData}
                  key={id}
                />
              </>
            );
          }

          if (id === "email") {
            return (
              <SwitchContainerForEmail
                setFormData={setFormData}
                formData={formData}
                key={id}
              />
            );
          }
        }
        return null;
      })}

      <div className={classes.Buttons}>
        <CustomButton
          className={classes.CustomButton}
          type="submit"
          disabled={isDisabled}
        >
          Update Notification
        </CustomButton>

        <Button
          onClick={() => {
            setOpenEditDialog(false);
            setNotificationId(false);
          }}
        >
          Cancel
        </Button>
      </div>
    </form>
  );
};

const EditNotificationPopup = ({ businessId }) => {
  const { isOpenEditDialog, setOpenEditDialog, selectedNotificationId } =
    useSystemSchedule();

  const { setErrorMessage } = useToastMessage();

  const [formData, setFormData] = useState({
    title: {
      type: "text",
      value: "",
      placeholder: "Notification Title",
      required: true,
      id: 1,
    },
    time: {
      type: "date",
      formate: "hours",
      value: new Date(),
      required: true,
      id: 2,
    },
    days: {
      type: "selection",
      id: 3,
    },
    whom: {
      type: "dropdown",
      value: "",
      options: [
        { name: "For LMS Dealers", value: "lms", id: 1 },
        { name: "For Team Heads", value: "team-head", id: 2 },
        { name: "For Department Heads", value: "department-head", id: 3 },
        { name: "For Admin", value: "business-admin", id: 4 },
      ],
      placeholder: "To Whom?",
      id: 4,
    },
    range: {
      type: "dropdown",
      value: "",
      options: [
        { name: "1 Hour", value: 1, id: 1 },
        { name: "2 Hours", value: 2, id: 2 },
        { name: "3 Hours", value: 3, id: 3 },
        { name: "4 Hours", value: 4, id: 4 },
        { name: "5 Hours", value: 5, id: 5 },
        { name: "6 Hours", value: 6, id: 6 },
        { name: "7 Hours", value: 7, id: 7 },
        { name: "8 Hours", value: 8, id: 8 },
        { name: "9 Hours", value: 9, id: 9 },
        { name: "10 Hours", value: 10, id: 10 },
        { name: "11 Hours", value: 11, id: 11 },
        { name: "13 Hours", value: 13, id: 13 },
        { name: "14 Hours", value: 14, id: 14 },
        { name: "15 Hours", value: 15, id: 15 },
        { name: "16 Hours", value: 16, id: 16 },
        { name: "17 Hours", value: 17, id: 17 },
        { name: "18 Hours", value: 18, id: 18 },
        { name: "19 Hours", value: 19, id: 19 },
        { name: "20 Hours", value: 20, id: 20 },
        { name: "21 Hours", value: 20, id: 21 },
        { name: "22 Hours", value: 20, id: 22 },
        { name: "23 Hours", value: 20, id: 23 },
        { name: "24 Hours", value: 20, id: 24 },
      ],
      placeholder: "Select Range",
      id: 5,
    },
    sms: {
      type: "switch",
      message: `Dear *name*, You have *quantity* new enquiries on https://easysocial.io. For HELP msg on www.e-so.in/helpdesk`,
      disabled: true,
      status: false,
      id: 6,
    },
    email: {
      type: "switch",
      status: false,
      subject: "",
      message: "",
      id: 7,
    },
  });

  const [date, setDate] = useState({ to: new Date(), from: new Date() });

  const [days, setDays] = useState({
    options: [
      { name: "Sun", id: "Sunday", active: false },
      { name: "Mon", id: "Monday", active: false },
      { name: "Tue", id: "Tuesday", active: false },
      { name: "Wed", id: "Wednesday", active: false },
      { name: "Thu", id: "Thursday", active: false },
      { name: "Fri", id: "Friday", active: false },
      { name: "Sat", id: "Saturday", active: false },
    ],
  });

  const variants = {
    open: { opacity: 1, x: "0%", y: "0%", pointerEvents: "all" },
    closed: { opacity: 0, x: "-50", y: "100%", pointerEvents: "none" },
  };

  const getNotificationRef = useRef(async (id) => {
    try {
      const res = await getNotificationById(id, businessId);

      if (res.error) {
        return setErrorMessage(res.error);
      }

      const data = res.data;

      const updatedOptions = days.options.map((day, i) => {
        if (data.days.find((e) => e === day.id)) {
          return (day.active = true);
        }
        day.active = false;
      });

      setDays({ ...days, ...updatedOptions });

      setFormData({
        ...formData,
        title: { ...formData.title, value: data.name },
        time: {
          ...formData.time,
          value: new Date(
            new Date(data.time).setTime(
              new Date().setHours(
                new Date(data.time).getHours(),
                new Date(data.time).getMinutes()
              )
            )
          ),
        },
        whom: { ...formData.whom, value: data.type },
        range: {
          ...formData.range,
          value: data.reminder_duration,
        },
        sms: {
          ...formData.sms,
          status: data.sms_message.active,
          message: data.sms_message.message,
        },
        email: {
          ...formData.email,
          status: data.email_message.active,
          subject: data.email_message.subject,
          message: data.email_message.message,
        },
      });
    } catch (err) {
      return setErrorMessage(err.message);
    }
  });

  useEffect(() => {
    if (isOpenEditDialog && selectedNotificationId !== null) {
      getNotificationRef.current(selectedNotificationId);
    }
  }, [isOpenEditDialog, selectedNotificationId]);

  return (
    <>
      <Backdrop
        show={isOpenEditDialog}
        clicked={() => {
          return setOpenEditDialog(false);
        }}
      />
      <motion.div
        initial={{ opacity: 0, x: "0%", y: "100%", pointerEvents: "none" }}
        transition={{ duration: 0.5, ease: [0.43, 0.13, 0.23, 0.96] }}
        animate={isOpenEditDialog ? "open" : "closed"}
        variants={variants}
        className={classes.EditNotification}
      >
        <h1>Update LMS Notification</h1>

        <div className={classes.Container}>
          <FormElements
            setFormData={setFormData}
            formData={formData}
            setDate={setDate}
            date={date}
            setDays={setDays}
            days={days}
          />
        </div>
      </motion.div>
    </>
  );
};

export default EditNotificationPopup;
