import {
  Input,
  List,
  ListItemText,
  MenuItem,
  Select,
  TextField,
  Switch,
  withStyles,
  FormControlLabel,
  Button,
} from "@material-ui/core";
import { MuiPickersUtilsProvider, TimePicker } from "@material-ui/pickers";
import React from "react";
import classes from "./AddNotificationPopup.module.css";
import DateFnsUtils from "@date-io/date-fns";
import "./global.css";
import { useSystemSchedule } from "../../../contexts/SystemScheduleContext";
import { ListItem } from "@material-ui/core";
import clsx from "clsx";
import { CustomButton } from "../../exports";
import { updateObject } from "../../../Shared/Utility";
import { Backdrop } from "../../exports";
import { motion } from "framer-motion";
import {
  createNotification,
  getNotifications,
} from "../../../api/systemSchedule";
import { useToastMessage } from "../../../contexts/ToastMessageContext";

const IOSSwitch = withStyles((theme) => ({
  root: {
    width: 42,
    height: 26,
    padding: 0,
    margin: theme.spacing(1),
    marginRight: 0,
  },
  switchBase: {
    padding: 1,
    color: theme.palette.common.white,
    "&$checked": {
      transform: "translateX(16px)",
      color: theme.palette.common.white,
      "& + $track": {
        backgroundColor: "#12B0EE",
        opacity: 1,
        border: "none",
      },
    },
    "&$focusVisible $thumb": {
      color: "#12B0EE",
      border: "6px solid #fff",
    },
  },
  thumb: {
    width: 24,
    height: 24,
  },
  track: {
    borderRadius: 26 / 2,
    border: `1px solid ${theme.palette.grey[400]}`,
    backgroundColor: theme.palette.grey[50],
    opacity: 1,
    transition: theme.transitions.create(["background-color", "border"]),
  },
  checked: {},
  focusVisible: {},
}))(({ classes, ...props }) => {
  return (
    <Switch
      focusVisibleClassName={classes.focusVisible}
      disableRipple
      classes={{
        root: classes.root,
        switchBase: classes.switchBase,
        thumb: classes.thumb,
        track: classes.track,
        checked: classes.checked,
      }}
      {...props}
    />
  );
});

const SelectDaysComponent = () => {
  const { days, setDays } = useSystemSchedule();

  const handleSelectActiveDays = (index) => {
    const options = days.options;

    options[index] = {
      ...options[index],
      active: !options[index].active,
    };

    return setDays({ ...days, options: options });
  };

  return (
    <div className={classes.SelectDays_Container}>
      <h1>Select Days</h1>
      <List className={classes.List}>
        {days.options.map((option, i) => {
          const active = option.active;
          return (
            <ListItem
              onClick={() => handleSelectActiveDays(i)}
              button
              key={option.id}
              className={classes.ListItem}
            >
              <ListItemText
                className={clsx(classes.ListItemText, active && classes.Active)}
              >
                {option.name}
              </ListItemText>
            </ListItem>
          );
        })}
      </List>
    </div>
  );
};

const DropdownForToWhom = ({ placeholder, id }) => {
  const { formData, setFormData } = useSystemSchedule();

  const handleChange = (e) => {
    return setFormData({
      ...formData,
      [id]: { ...formData[id], value: e.target.value },
    });
  };

  return (
    <div className={classes.DropdownForToWhom}>
      <Select
        required={true}
        value={formData[id].value}
        onChange={handleChange}
        displayEmpty
        className={classes.Select}
        inputProps={{ "aria-label": "Without label" }}
      >
        <MenuItem value="" className={classes.Option_TOWhom} disabled>
          {placeholder}
        </MenuItem>
        {formData[id].options.map((option) => {
          return (
            <MenuItem key={option.id} value={option.value}>
              {option.name}
            </MenuItem>
          );
        })}
      </Select>
    </div>
  );
};

const SwitchContainerForSMS = () => {
  const { formData, setFormData } = useSystemSchedule();

  const onChange = (key, id, e) => {
    return setFormData({
      ...formData,
      [key]: { ...formData[key], [id]: e.target.value },
    });
  };

  const handleSwitch = (key, e) => {
    return setFormData({
      ...formData,
      [key]: { ...formData[key], status: e.target.checked },
    });
  };

  const status = formData.sms.status;

  return (
    <div className={classes.SwitchContainerForSMS}>
      <div className={classes.Switch_Box}>
        <h1>SMS</h1>
        <FormControlLabel
          control={
            <IOSSwitch
              onChange={(e) => handleSwitch("sms", e)}
              checked={formData.sms.status}
              name="checkedB"
            />
          }
        />
      </div>

      {status && (
        <>
          <TextField
            disabled={formData.sms.disabled}
            required={true}
            className={classes.Input}
            placeholder="Message"
            value={formData.sms.message}
            onChange={(e) => onChange("sms", "message", e)}
            multiline
            rows="3"
          />
        </>
      )}
    </div>
  );
};

const SwitchContainerForEmail = () => {
  const { formData, setFormData } = useSystemSchedule();

  const onChange = (key, id, e) => {
    return setFormData({
      ...formData,
      [key]: { ...formData[key], [id]: e.target.value },
    });
  };

  const handleSwitch = (key, e) => {
    return setFormData({
      ...formData,
      [key]: { ...formData[key], status: e.target.checked },
    });
  };

  const status = formData.email.status;

  return (
    <div className={classes.SwitchContainerForEmail}>
      <div className={classes.Switch_Box}>
        <h1>Email</h1>
        <FormControlLabel
          control={
            <IOSSwitch
              onChange={(e) => handleSwitch("email", e)}
              checked={formData.email.status}
              name="checkedB"
            />
          }
        />
      </div>
      {status && (
        <>
          <TextField
            required={true}
            className={classes.Input}
            placeholder="Subject"
            value={formData.email.subject}
            onChange={(e) => onChange("email", "subject", e)}
            multiline
            rows="1"
          />
          <TextField
            required={true}
            className={classes.Input}
            placeholder="Message"
            value={formData.email.message}
            onChange={(e) => onChange("email", "message", e)}
            multiline
            rows="3"
          />
        </>
      )}
    </div>
  );
};

const FormElements = ({ businessId }) => {
  const {
    formData,
    setFormData,
    days,
    setOpenAddNotification,
    addNotifications,
  } = useSystemSchedule();

  const { setErrorMessage, setSuccessMessage, isDisabled, setDisabled } =
    useToastMessage();

  const inputChangedHandler = (event, inputIdentifier) => {
    let updatedFormElement;

    updatedFormElement = updateObject(formData[inputIdentifier], {
      value: event.target.value,
    });

    const updatedOrderForm = updateObject(formData, {
      [inputIdentifier]: updatedFormElement,
    });

    setFormData(updatedOrderForm);
  };

  const onSubmit = async (event) => {
    event.preventDefault();

    setDisabled();

    const activeDays = [];

    days.options.filter((option) => {
      if (option.active) {
        activeDays.push(option.id);
      }

      return days;
    });

    const data = {
      name: formData.title.value,
      days: activeDays,
      reminder_duration: formData.range.value,
      status: true,
      sms_message: {
        active: formData.sms.status,
        message: formData.sms.message,
      },
      email_message: {
        active: formData.email.status,
        message: formData.email.message,
        subject: formData.email.subject,
      },
      time: formData.time.value,
      scheduled_for: "all",
      type: formData.whom.value,
    };

    const res = await createNotification(data, businessId);

    if (res.error) {
      return setErrorMessage(res.error);
    }

    const resGet = await getNotifications(businessId);

    if (resGet.error) {
      return setErrorMessage("resGet.error!");
    }

    addNotifications(resGet.data);

    setOpenAddNotification(false);

    return setSuccessMessage("Notification successfully created!");
  };

  const formElementsArray = [];

  for (let key in formData) {
    formElementsArray.push({
      id: key,
      config: formData[key],
    });
  }

  return (
    <form className={classes.Form_Container} onSubmit={onSubmit}>
      {formElementsArray.map((formElement) => {
        const type = formElement.config.type;
        const id = formElement.id;

        if (type === "text" || type === "number" || type === "email") {
          const value = formElement.config.value;
          const placeholder = formElement.config.placeholder;
          return (
            <div key={id} className={classes.InputContainer}>
              <Input
                onChange={(e) => {
                  inputChangedHandler(e, id);
                }}
                required={formElement.config.required}
                className={classes.Input}
                value={value}
                placeholder={placeholder}
              />
            </div>
          );
        }

        if (type === "date") {
          const value = formElement.config.value;

          return (
            <div key={id} className={classes.TimePicker_Container}>
              <h1>What time of the Day?</h1>

              <MuiPickersUtilsProvider
                utils={DateFnsUtils}
                className={classes.TimePicker}
              >
                <TimePicker
                  className={classes.Picker}
                  margin="normal"
                  id="time-picker"
                  onChange={(date) =>
                    inputChangedHandler({ target: { value: date } }, id)
                  }
                  value={value || new Date()}
                  KeyboardButtonProps={{
                    "aria-label": "change time",
                  }}
                  placeholder="What time of the day?"
                />
              </MuiPickersUtilsProvider>
            </div>
          );
        }

        if (type === "selection") {
          return <SelectDaysComponent key={id} />;
        }

        if (type === "dropdown") {
          return (
            <DropdownForToWhom
              placeholder={formElement.config.placeholder}
              id={id}
              key={id}
            />
          );
        }

        if (type === "switch") {
          if (id === "sms") {
            return (
              <>
                <SwitchContainerForSMS key={id} />
              </>
            );
          }

          if (id === "email") {
            return <SwitchContainerForEmail key={id} />;
          }
        }
        return null;
      })}

      <div className={classes.Buttons}>
        <CustomButton
          disabled={isDisabled}
          className={classes.CustomButton}
          type="submit"
        >
          Add Notification
        </CustomButton>

        <Button
          disabled={isDisabled}
          onClick={() => setOpenAddNotification(false)}
        >
          Cancel
        </Button>
      </div>
    </form>
  );
};

const AddNotificationPopup = ({ businessId }) => {
  const { isOpenAddNotification, setOpenAddNotification } = useSystemSchedule();

  const variants = {
    open: { opacity: 1, x: "0%", y: "0%", pointerEvents: "all" },
    closed: { opacity: 0, x: "-50", y: "100%", pointerEvents: "none" },
  };

  return (
    <>
      <Backdrop
        show={isOpenAddNotification}
        clicked={() => setOpenAddNotification(false)}
      />
      <motion.div
        initial={{ opacity: 0, x: "0%", y: "100%", pointerEvents: "none" }}
        transition={{ duration: 0.5, ease: [0.43, 0.13, 0.23, 0.96] }}
        animate={isOpenAddNotification ? "open" : "closed"}
        variants={variants}
        className={classes.AddNotification}
      >
        <h1>New LMS Notification</h1>
        <div className={classes.Container}>
          <FormElements businessId={businessId} />
        </div>
      </motion.div>
    </>
  );
};

export default AddNotificationPopup;
