import React, { useEffect, useRef } from "react";
import { Modal2 } from "../../Components/exports";
import AddNotificationPopup from "../../Components/SystemSchedule/AddNotificationPopup/AddNotificationPopup";
import DeleteNotificationPopup from "../../Components/SystemSchedule/DeleteNotificationPopup/DeleteNotificationPopup";
import EditNotificationPopup from "../../Components/SystemSchedule/EditNotificationPopup/EditNotificationPopup";
import NotificationLists from "../../Components/SystemSchedule/NotificationList/NotificationList";
import { useBusinessProfile } from "../../contexts/BusinessProfileContext";
import { useLoader } from "../../contexts/LoaderContext";
import SystemScheduleContext, {
  useSystemSchedule,
} from "../../contexts/SystemScheduleContext";
import { useToastMessage } from "../../contexts/ToastMessageContext";
import { getNotifications } from "../../api/systemSchedule";
import classes from "./SystemSchedule.module.css";

const SystemSchedulePage = (props) => {
  const { businessProfile, setBusinessId } = useBusinessProfile();
  const { setErrorMessage } = useToastMessage();
  const { addNotifications } = useSystemSchedule();
  const { startLoader, cancelLoader } = useLoader();

  const businessProfileRef = useRef(() => {
    const businessId =
      props.match.params.businessId || localStorage.getItem("businessId");
    return setBusinessId(businessId);
  });

  const businessId = props.match.params.businessId;

  const getAllNotificationsRef = useRef(async () => {
    startLoader();
    const res = await getNotifications(businessId);

    if (res.error) {
      cancelLoader();
      return setErrorMessage("You may not have created notifications!");
    }

    cancelLoader();

    return addNotifications(res.data);
  });

  useEffect(() => {
    businessProfileRef.current();
    getAllNotificationsRef.current();
  }, []);

  return (
    <section className={classes.System_Schedule}>
      <Modal2 profile={businessProfile} title="System Schedule" />

      <NotificationLists businessId={businessId} />
      <DeleteNotificationPopup businessId={businessId} />
      <AddNotificationPopup businessId={businessId} />
      <EditNotificationPopup businessId={businessId} />
    </section>
  );
};

const SystemScheduleContainer = (props) => {
  return (
    <SystemScheduleContext>
      <SystemSchedulePage {...props} />
    </SystemScheduleContext>
  );
};

export default SystemScheduleContainer;
