import { Button, ListItem } from "@material-ui/core";
import { List } from "@material-ui/core";
import React from "react";
import classes from "./DeleteNotificationPopup.module.css";
import { Backdrop, CustomButton } from "../../exports";
import { useSystemSchedule } from "../../../contexts/SystemScheduleContext";
import { motion } from "framer-motion";
import { tConvert } from "../../../Shared/Utility";
import { useToastMessage } from "../../../contexts/ToastMessageContext";
import {
  deleteNotificationById,
  getNotifications,
} from "../../../api/systemSchedule";

const ItemToDelete = ({ title, text }) => {
  return (
    <ListItem className={classes.ListItem}>
      <div>
        <h6>{title}</h6>
        <p>{text}</p>
      </div>
    </ListItem>
  );
};

const DeleteNotificationPopup = ({ businessId }) => {
  const {
    isOpenDeleteDialog,
    setOpenDeleteDialog,
    selectedNotificationId,
    notificationsList,
    addNotifications,
    setNotificationId,
  } = useSystemSchedule();

  const { setErrorMessage, setSuccessMessage, isDisabled, setDisabled } =
    useToastMessage();

  const variants = {
    open: { opacity: 1, x: "0%", y: "0%", pointerEvents: "all" },
    closed: { opacity: 0, x: "-50", y: "100%", pointerEvents: "none" },
  };

  const data = notificationsList.find((e) => e._id === selectedNotificationId);

  const getText = () => {
    const time = tConvert(
      `${new Date(data.time).getHours()}:${new Date(data.time).getMinutes()}`
    );

    const days = data.days.map((day) => day.replace("day", "").toLowerCase());

    const team_members = data.scheduled_for;

    const duration_to = `${data.reminder_duration} Hours`;

    const smsStatus = data.sms_message.active;
    const emailStatus = data.email_message.active;

    return `${time} - ${days.join(
      ", "
    )}, Team Members - ${team_members}, ${duration_to}${
      smsStatus ? " - SMS" : " "
    } - ${emailStatus ? ", Email" : " "}`;
  };

  return (
    <>
      <Backdrop
        show={isOpenDeleteDialog}
        clicked={() => setOpenDeleteDialog(false)}
      />
      <motion.div
        initial={{ opacity: 0, x: "0%", y: "100%", pointerEvents: "none" }}
        transition={{ duration: 0.5, ease: [0.43, 0.13, 0.23, 0.96] }}
        animate={isOpenDeleteDialog ? "open" : "closed"}
        variants={variants}
        className={classes.DeleteNotificationPopup}
      >
        <h1>Deleting Notification</h1>

        <List className={classes.List}>
          <ItemToDelete title={data && data.name} text={data && getText()} />
        </List>

        <div className={classes.Buttons}>
          <CustomButton
            disabled={isDisabled}
            onClick={async () => {
              try {
                setDisabled();
                const res = await deleteNotificationById(
                  selectedNotificationId,
                  businessId
                );

                if (res.error) {
                  return setErrorMessage(res.error);
                }

                const resGet = await getNotifications();

                if (resGet.error) {
                  return setErrorMessage(
                    "You may not have created notifications!"
                  );
                }

                addNotifications(resGet.data);

                setOpenDeleteDialog(false);

                setNotificationId(null);

                return setSuccessMessage("Notification successfully removed!");
              } catch (err) {
                setOpenDeleteDialog(false);
                setNotificationId(null);

                return setErrorMessage(
                  "Internal Server occurs while removing notification!"
                );
              }
            }}
          >
            Delete Now
          </CustomButton>
          <Button
            disabled={isDisabled}
            onClick={() => setOpenDeleteDialog(false)}
          >
            Cancel
          </Button>
        </div>
      </motion.div>
    </>
  );
};

export default DeleteNotificationPopup;
